
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Badge, Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';
import Pagination from '../Base/Paginations/Pagnations';
import {getAllRoles} from './../Apis.js'
//import rolesData from './rolesData'
const addrole = 'role/add'

function RoleRow(props) {
  const roles = props.roles
  const roleLink = `/roles/${roles.id}`
  const updaterole = `role/update/${roles.id}`
 

  const getBadge = (status) => {
    return status === 'Active' ? 'success' :
      status === 'Inactive' ? 'secondary' :
        status === 'Pending' ? 'warning' :
          status === 'Banned' ? 'danger' :
            'primary'
  }

  return (
    <tr key={roles.id.toString()}>
      <th scope="row"><Link to={roleLink}>{roles.id}</Link></th>
      <td ><Link to={roleLink}>{roles.name}</Link></td>
      <td>{roles.status==1?'Enable':'Disable'}</td>
      {/* <td >{roles.email}</td> */}
      <td ><Link to={updaterole}><span class="cui-pencil" aria-hidden="true"></span></Link></td>
    </tr>
  )
}

class Roles extends Component {

  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      totalPages: 0,
      totalRecords: 0
    };
  }

  componentDidMount() {
    this.getAllRoles(1)
  
  }
  getAllRoles=async(page)=>{
    const responseData= await getAllRoles(page);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          
              isLoaded: true,
              items: responseData.data.result,
              totalPages: responseData.data.pages,
              totalRecords: responseData.data.count
            
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  onPageChanged = data => {
    //const { allCountries } = this.state;
   
    const { currentPage, totalPages, pageLimit } = data;
    this.getAllRoles(currentPage)
    console.log(data);
  };

  render() {

    const roleList = this.state.items; //rolesData.filter((role) => role.id < 10)
    const Totrecords = parseInt(this.state.totalRecords); // total roles records.
    let pagination;

    if(Totrecords !== 0 ){
      pagination = <Pagination totalRecords={Totrecords} pageLimit={5} pageNeighbours={1} onPageChanged={this.onPageChanged} />
    }

    return (
      <div className="animated fadeIn">
        <Row>
          <Col xl={2}>
          </Col>
          <Col xl={8}>
            <Card>
              <CardHeader>
                <i className="fa fa-align-justify"></i> Roles 
                <Link to={addrole}><button onClick={this.showroleInfo} className='btn btn-success  upActionbtn'>+ Add Role</button></Link>
              </CardHeader>
              <CardBody>
                <Table responsive hover>
                  <thead>
                    <tr>
                      <th scope="col">Id</th>
                      <th scope="col">Name</th>
                      <th scope="col">Status</th>
                      {/* <th scope="col">Email</th> */}
                      <th scope="col">Edit role</th>
                    </tr>
                  </thead>
                  <tbody>
                    {roleList.map((roles, index) =>
                      <RoleRow key={index} roles={roles}/>
                    )}
                  </tbody>
                </Table>
              </CardBody>              
              {pagination}
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default Roles;
